import React, { useEffect } from "react";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Checkbox from "@material-ui/core/Checkbox";
import { Grid } from "@material-ui/core";
import useScrollable from "../../utils/useScrollable";
import { useSelector, useDispatch } from "react-redux";
import {
  fetchUnAssignedPermissions,
  assignRolePermissions
} from "../../store/actions";
import InfiniteScroll from "react-infinite-scroller";
import SaveIcon from "@material-ui/icons/Save";
import Button from "@material-ui/core/Button";
export default function AssignRolePermission(props) {
  const permissions = useSelector(state => state.permissions);
  const dispatch = useDispatch();
  const [checkedPermissions, setCheckedPermissions] = React.useState({});
  const [checkAll, setCheckAll] = React.useState(false)
  const scroll = useScrollable(
    fetchUnAssignedPermissions,
    props.match.params.id
  );
  const handleChange = id => event => {
    setCheckedPermissions({
      ...checkedPermissions,
      [id]: {
        ...checkedPermissions[id],
        checked: !checkedPermissions[id].checked
      }
    });
  };
  const switchAllPermissions = (value) => {
    const permissionsChecked = permissions.data.reduce(
      (prevValue, currentValue) => {
        prevValue[currentValue.id] = {
          checked: value,
          name: currentValue.name
        };
        return prevValue;
      },
      {}
    );
    setCheckedPermissions(permissionsChecked);
  };
  const onCheckAllChange = () => {
    if (checkAll) {
      switchAllPermissions(false);
    }
    else {
      switchAllPermissions(true);
    }
    setCheckAll(!checkAll)
  }
  const onCancelClicked = () => {
    switchAllPermissions(false);
    setCheckAll(false)
  }
  const saveRolePermissions = () => {
    const filteredPermissions = Object.keys(checkedPermissions).filter(e => {
      return checkedPermissions[e].checked === true;
    });
    dispatch(assignRolePermissions(props.match.params.id, filteredPermissions));
  };
  useEffect(() => {
    const permissionsChecked = permissions.data.reduce(
      (prevValue, currentValue) => {
        prevValue[currentValue.id] = {
          checked: false,
          name: currentValue.name
        };
        return prevValue;
      },
      {}
    );
    setCheckedPermissions(permissionsChecked);
  }, [permissions]);
  return (
    <div>
      <div style={{ display: 'flex', justifyContent: 'space-between' }}>
        <h3>Assign Permissions</h3>
        <FormControlLabel
          control={<Checkbox checked={checkAll} onChange={onCheckAllChange} name="checkAll" />}
          label="Check All"
        /></div>
      <InfiniteScroll
        pageStart={0}
        loadMore={scroll}
        hasMore={permissions.hasMore}
        loader={
          <div className="loader" key="loader">
            Loading ...
          </div>
        }
      >
        <Grid
          container
          style={{
            minHeight: "400px",
            overflowY: "scroll"
          }}
        >
          {Object.keys(checkedPermissions).map((e, i) => {
            return (
              <Grid item xs={3} key={i}>
                <FormControlLabel
                  control={
                    <Checkbox
                      checked={checkedPermissions[e].checked}
                      onChange={handleChange(e)}
                      value={e}
                    />
                  }
                  label={checkedPermissions[e].name}
                />
              </Grid>
            );
          })}
        </Grid>
      </InfiniteScroll>
      <div
        style={{
          display: "flex",
          flexDirection: "row-reverse"
        }}
      >
        <Button
          variant="contained"
          color="default"
          onClick={() => { onCancelClicked() }}
        >
          Cancel
        </Button>
        <Button
          variant="contained"
          color="primary"
          disabled={permissions.data.length === 0}
          onClick={saveRolePermissions}
          startIcon={<SaveIcon />}
        >
          Save
        </Button>
      </div>
    </div>
  );
}
