import * as actionTypes from "./actionTypes";
import API from "../../api";

//Districts
const storeRoles = (payload, page, pageSize) => {
  return {
    type: actionTypes.STORE_ROLES,
    payload,
    page,
    pageSize
  };
};

export const createRole = data => {
  return (dispatch, geState) => {
    const { roles } = geState();
    API.post(`/role`, data).then(response => {
      dispatch(fetchRoles(roles.page, roles.pageSize));
    });
  };
};

export const deleteRole = id => {
  return (dispatch, geState) => {
    const { roles } = geState();
    API.delete(`/role/${id}`).then(response => {
      dispatch(fetchRoles(roles.page, roles.pageSize));
    });
  };
};

export const fetchRoles = (page, pageSize) => {
  return dispatch => {
    API.get(`/role?page=${page + 1}&limit=${pageSize}`).then(response => {
      dispatch(storeRoles(response.data, page, pageSize));
    });
  };
};
